	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>\n<p>朝、今日は雨だと天気予報で言ってた</p>\n<ul>\n<li>↓一通り家の用事終えて外見たらまだ降ってない</li>\n<li>↓買い物行くチャンス</li>\n<li>↓バイク出した瞬間降りだす</li>\n<li>↓ヤバイ急がないと</li>\n</ul>\n</li>\n<li>↓段々雨が強まり土砂降りの中を走る走る走る</li>\n<li>↓結局ずぶ濡れになって到着</li>\n<li>↓買い物終えて外出ると空真っ青</li>\n<li>( ﾟдﾟ )\n<strong>仕事中に聞いた話</strong> 今日仕事でペア組んでる人の話だが、以前大型スーパーの建物解体工事に派遣されたらしく、そこの現場が聞くからに楽しそうなんだ。朝集合してちょっとした朝礼の後、一人一人にデカいハンマーが貸し出しされて「こいつで今から8時間、この建物内のモノ好きなだけぶっ壊してくれ」だと。実際石膏の壁とか手辺り次第破壊しまくって凄いスカッとしてストレス解消に効くって言うから是非行ってみたかったんだが…もう今は解体済の更地になってるんだね orz</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-04-08T15:47:00+09:00","strDate":"2006/4/8","title":"雨"}}},"pathContext":{"path":"/ja/blog/2006/04/08-%e9%9b%a8/","langKey":"ja"}}

